import React from 'react';
import me from './assets/me.jpg';

const AboutMe = () => {
  return (
    <div className="flex flex-col items-center bg-gray-100 text-gray-800 p-10">
      <h1 className="text-4xl font-bold mb-6">About Me</h1>
      <div className="flex flex-col items-center max-w-3xl text-center">
        <img src={me} alt="Your Name" className="rounded-full w-64 h-64 object-cover object-top mb-6 border-4 border-gray-300" />
        <p className="text-lg leading-relaxed text-gray-700 px-6">
        I currently live in and love exploring Central Texas. With years of experience in commercial photography, I am passionate about bringing my clients' visions to life. Whether it's capturing the essence of a brand or creating stunning visuals for marketing campaigns, I am dedicated to providing exceptional service and delivering high-quality results.
        </p>
      </div>
    </div>
  );
}

export default AboutMe;
